@font-face {
  font-family: 'Minisystem';
  src: url('./fonts/minisystem.ttf') format('truetype');
}

body{
  background: #2a3347;
  color: #e0d7d7;
}

.App {
  background: #3f4c6b;
  text-align: center;
  width: 800px;
  margin: 20px auto;
  border: 2px solid #e0d7d7;
  border-radius:20px;
  padding: 25px;
  display: block;
  box-shadow: 0px 10px 50px rgba(0,0,0,.3);
}
.transport-controls-wrapper span{
  display: inline-block;
  font-family: 'Minisystem';
  font-size: 30px;
  background-color: rgba(0,0,0,.8);
  padding: 10px;
  width: 150px;
  text-align: right;
}

.stop,
.play{
  appearance: none;
  border: 2px solid #e0d7d7;
  appearance: none;
  height: 50px;
  width: 75px;
  border-radius: 3px;
  background-color: #606c88;
  color: #e0d7d7;
  text-transform: uppercase;
  margin: 0 5px;
  font-weight:bolder;
  font-size:14px;
}

.play[data-active=true]{
  background: #e0d7d7;
  color: #3f4c6b;
}

.stop:active,
.play:active{
  opacity: .7;
}

.trackselection-wrapper{
  height: 60px;
  width: 450px;
  display:inline-block;
  margin: 30px 0;
}
.clearcontrols{
  display: inline-block;
  width: 100%;
  height: 50px;
}
.trackselector-wrapper{
  width:100px;
  height:35px;
  display:inline-block;
  padding:5px;
}

.trackselector-wrapper button,
.clearcontrols button{
  width: 100px;
  height: 30px;
  line-height: 25px;
  font-size: 12px;
  font-weight: 700;
  appearance: none;
  padding:none;
  text-transform: uppercase;
  border: 2px solid #e0d7d7;
  color: #e0d7d7;
  background-color:#606c88;
  -webkit-appearance: none;
  border-radius:2.5px;
  cursor: pointer;
}

.clearcontrols button{
  margin: 5px;
  width: 150px;
  background-color: #101010;
}

.trackselector-wrapper button[data-selected=true]{
  background-color: #e0d7d7;
  color: #606c88;
  transition: background-color .1s, color .1s; 
}
.volumeslider{
  display:inline-block;
  width:20px;
  height: 80px;
  padding: 0;
  margin: 0 2.5px;
}
.volumeslider input{
  width: 80px;
  height: 20px;
  margin: 0;
  transform-origin: 40px 40px;
  transform: rotate(-90deg);
}

.sequencer-wrapper{
  margin: 0 0 0;
  /* border:5px solid #aaa4a4; */
  border-radius: 8px;
  display:inline-block;
  padding: 0 50px;
}

.sequencer-button-wrapper{
  display:inline-block;
  margin: 0 0 10px;
}
.sequencer--button{
  position:relative;
  height:70px;
  width:70px;
  border:1px solid #000;
  display:inline-block;
  margin: 0 5px;
  font-size: 24px;
  line-height:70px;
  border-radius:5px;
  background: linear-gradient(to bottom, #606c88 0%,#3f4c6b 100%); 
  cursor: pointer;
}

.sequencer--button.active{
  background: linear-gradient(to bottom, #b6bac4 0%,#6e7177 100%);
  box-shadow: 0 0 10px rgba(255,255,255,.3);
}

.sequencer--button:active{
  opacity:.7;
}

.sequencer--button.chase,
.sequencer--button.active.chase{
  background: linear-gradient(to bottom, #ec3a3a 0%,#7d1a1a 100%);
}

.sequencer-button-wrapper:nth-of-type(4n+1) .sequencer--button::after{
  content:'';
  width: 50px;
  height: 5px;
  border-radius: 1.5px;
  display:block;
  position: absolute;
  bottom: 10px;
  left: 10px;
  background-color: #000;
}

.sequencer-button-wrapper label{
  position:relative;
  display:block;
  width:70px;
  text-align:center;
  margin: 5px 5px 0;
  font-size: 11px;
  color: #aaa4a4;
  font-weight:800;
  font-family: Helvetica, sans-serif;
}


.spinner{
  position: absolute;
  top: 50vh;
  left: 0;
  width:100%;
  text-align:center;
  z-index: 999;
  animation: spin 2s infinite linear;
}

@keyframes spin{
  0%{
    transform: rotate(0deg) scale(.1);
  }
  50%{
    transform: rotate(360deg) scale(2);
  }
  100%{
    transform: rotate(0deg) scale(.1);
  }
}